.Links {
  display: flex;
  flex-wrap: wrap;
  height: 220px;
  padding: 10px 0px 20px 15px;
  background: white;
  border-radius: 5px;
  justify-content: space-between;
  margin: 20px 0px 30px 0px;
}
@media all and (min-width: 375px) and (max-width: 1100px) {
  .Links {
    height: auto;
  }
}
