.Employee img {
  width: 120px;
  object-fit: cover;
}

.Employee-info h2 {
  white-space: pre-line;
  font-size: 1.1em;
}
.Employee-info p {
  color: #212121;
  margin-top: 0;
}
.Employee {
  display: flex;
  margin-bottom: 20px;
}

.Employee-info {
  margin-left: 20px;
}

.Employee-contact img {
  width: 20px;
  height: 20px;
}

.Employee-contact {
  display: flex;
}

.Employee-contact p {
  margin-left: 10px;
  align-self: end;
  margin-bottom: 0;
  margin-top: 0;
}
.Employee-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Employee-info h2 {
  margin: 0;
}
