.Comment-input {
  border: 2px solid #e1e1e1;
  border-radius: 5px;
}

.Comment-input:active,
.Comment-input:focus {
  border-color: #03a9f4;
}

.Comment-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Comment-avatar img {
  width: 40px;
  height: 40px;
  padding-right: 10px;
  border-radius:40px;
}

.Comment-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 30px 0 30px 0;
  position: relative;
}

.Comment-btn {
  display: flex;
  justify-content: end;
  align-items: center;
}

.Comment-cancel-btn {
  margin-right: 0;
  background: none;
  color: #03a9f4;
}

.Comment-send-btn {
  background: #03a9f4;
}

.Comment-send-btn:hover,
.Comment-send-btn:active {
  background: #0288d1;
}
.Comment-cancel-btn:hover,
.Comment-cancel-btn:active {
  background: #e3e3e3;
}
.Comment-cancel-btn,
.Comment-send-btn {
  margin-top: 30px;
}

.Comment-emoji-btn {
  background: transparent;
  outline: none;
  border: none;
  display: flex;
}

.Comment-emoji-container div {
  position: absolute;
  right: 0;
  z-index:1
}

.Comment-emoji-container {
  margin-top: 10px;
}

.Comment-emoji-btn-icon {
  stroke: #bbbcc2;
  transition: stroke 0.3s ease;
  width: 33px;
  height: 33px;
  margin-top: 30px;
  padding-right: 5px;
  cursor: pointer;
}

.Comment-emoji-btn-icon:hover {
  stroke: #e54541; 
}
