.Popup-msg {
  display: flex;
  flex-direction: column;
  width: 380px;
  border-radius: 5px;
  background-color: white;
  animation: show 1.1s;
  border: 1px solid transparent;
  background: #fafafa;
  padding: 25px;
  display: flex;
  margin: 100px;
}

.Popup-btn-yes {
  background: #1fab89;
  min-width: 150px;
  height: 30px;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  margin-top: 30px;
  align-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.Popup-msg p {
  text-align: center;
}
.Popup-overlay {
  width: 100%;
  height: 110vh;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: show;
}

.Popup-btn {
  display: flex;
  justify-content: space-evenly;
}

.Popup-btn-no {
  background: none;
  min-width: 150px;
  height: 30px;
  color: #1fab89;
  border: none;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
  box-shadow: inherit;
  cursor: pointer;
  margin-top: 30px;
  align-self: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

.Popup-btn-no:hover {
  background: #e3e3e3;
  border-radius: 5px;
}

.Popup-btn-yes:hover {
  background: #126a54;
}

@keyframes show {
  0% {
    transform: translateY(-500px);
  }
}
